import React from 'react'

const Default = () => (
  <div>
    <h1>Package browser</h1>
    <p>On a Debian and Ubuntu systems, there is a file called
    /var/lib/dpkg/status that holds information about software packages
    that the system knows about.</p>
    <p>By clicking the items in the navigation bar you can inspect these packages.</p>
  </div>
)

export default Default



