import React from 'react'
import '../styles/Packages.css'

const Packages = ({ packages, handleClick }) => (
  <div>
    {packages.map(name =>
      <div className='Packages-package' key={name} onClick={() => handleClick(name)}>{name}</div>
    )}
  </div>
)

export default Packages



