import axios from 'axios'

const baseUrl = '/api/packages'

const getPackages = async () => {
  return await axios.get(baseUrl)
}

const getPackage = async (name) => {
  const res = await axios.get(`${baseUrl}/${name}`)
  const data = res.data
  return data
}

export default { getPackages, getPackage }



