import React from 'react'
import '../styles/Info.css'

const Info = ({ information, handleClick }) => (
  <div>
    <div className='Info-name'>{information.name}</div>
    <div className='Info-description'>{information.description}</div>
    <div className='Info-dependencies'>
      {information.dependencies[0].length || information.dependencies[1].length
        ? <div className='Info-dependencies-title'>Dependencies ({information.dependencies[0].length + information.dependencies[1].length})</div>
        : null}
      {information.dependencies[0].map(dependency =>
        <span className='Info-dependency Info-dependency-clickable' key={information.name + ', dependency: ' + dependency} onClick={() => handleClick(dependency)}>{dependency}</span>
      )}
      {information.dependencies[1].map(dependency =>
        <span className='Info-dependency Info-dependency-unclickable' key={information.name + ', dependency: ' + dependency}>{dependency}</span>
      )}
    </div>
    <div className='Info-dependencies'>
      {information.dependents.length ? <div className='Info-dependencies-title'>Reverse dependencies ({information.dependents.length})</div> : null}
      {information.dependents.map(dependent =>
        <span className='Info-dependency Info-dependency-clickable' key={information.name + ', dependent: ' + dependent} onClick={() => handleClick(dependent)}>{dependent}</span>
      )}
    </div>
  </div>
)

export default Info



